html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

:root {
  --sky-color: #00acdc;
}

body {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  line-height: 60px;
}

ul, .header, .nav__link {
  display: flex;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
}

.nav {
  text-transform: uppercase;
}

.nav__item {
  margin-right: 20px;
  list-style: none;
  position: relative;
}

.nav__item:last-child {
  margin: 0;
}

.nav__link {
  color: #000;
  height: 100%;
  text-decoration: none;
}

.nav__link:hover, .is-active {
  color: var(--sky-color);
}

.is-active:after {
  content: "";
  background-color: var(--sky-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
